.about {
  padding: 140px 0;
  background-color: var(--neutral-bg);
  transition: padding var(--trs-35);

  @include mobile {
    padding: 100px 0;
  }

  &__container {
    gap: 20px;

    @include desktop {
      gap: 50px 0;
    }
  }

  &__picture {
    grid-column: 6 span;
    position: relative;
    left: 30px;
    display: block;
    width: 100%;
    max-width: 540px;
    min-height: 100%;
    border: 10px solid hsl(43, 100%, 63%);
    transition: left var(--trs-35);

    @include desktop {
      grid-column: 12 span;
      margin-inline: auto;
    }

    @include small-tablet {
      left: 0px;
    }
  }

  &__img {
    position: relative;
    top: -30px;
    left: -30px;
    box-shadow: 0px 0px 20px 0px hsla(217, 57%, 25%, 0.3);
    transition: left var(--trs-35);

    @include small-tablet {
      left: 0px;
    }
  }

  &__content {
    grid-column: 6 span;
    align-self: center;

    @include desktop {
      grid-column: 12 span;
      margin-inline: auto;
    }
  }

  &__title {
    color: var(--primary-color);
    font-family: var(--ff-secondary);
    font-size: var(--fs-42);
    font-weight: 400;
    margin: 0;
    margin-bottom: 23px;
  }

  &__descr {
    color: var(--neutral-300);
    font-family: var(--ff-primary);
    font-size: var(--fs-19);
    font-weight: 400;
    width: 100%;
    max-width: 559px;
    margin: 0;
    margin-top: 23px;
    margin-bottom: 34px;

    @include desktop {
      max-width: 886px;
    }
  }

  &__contact {
    display: inline-flex;
    gap: 40px;
    align-items: center;
    padding: 15px 40px 15px 20px;
    background-color: var(--primary-color);

    @include mobile {
      width: 100%;
    }
  }
}

.characteristic {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
  margin-bottom: 55px;

  @include mobile {
    flex-direction: column;
  }

  &__item {
    display: flex;
    flex-direction: column;

    @include mobile {
      flex-direction: row;
      align-items: baseline;
      gap: 20px;
    }
  }

  &__value {
    color: var(--primary-color);
    font-family: var(--ff-secondary);
    font-size: var(--fs-54);
    font-weight: 400;
    margin: 0;
  }

  &__meta {
    color: var(--neutral-300);
    font-family: var(--ff-primary);
    font-size: var(--fs-17);
    font-weight: 400;
    margin: 0;
  }

  &__line {
    width: 2px;
    background-color: var(--accent-color);
    display: inline-block;
    margin: 0 25px;


    @include mobile {
      width: 100%;
      height: 2px;
      margin: 0;
    }
  }
}

.qar-code {
  @include mobile {
    display: none;
  }
}

.address {
  font-style: normal;

  &__name {
    display: block;
    color: var(--accent-color);
    font-family: var(--ff-secondary);
    font-size: var(--fs-22);
    font-weight: 400;
    margin: 0;
    margin-bottom: 10px;
  }

  &__mail,
  &__tel {
    color: var(--neutral-200);
    font-family: var(--ff-primary);
    font-size: var(--fs-18);
    font-weight: 400;

    a {
      color: var(--neutral-200);
      transition: color var(--trs-35);

      @include hover {
        color: var(--neutral-100);
      }
    }
  }
}
