.hero {
  padding: 100px 0;
  background-color: var(--primary-color);
  transition: padding var(--trs-35);

  @include mobile {
    padding: 50px 0;
  }

  &__container {
    gap: 70px;

    @include desktop {
      gap: 50px 0;
    }
  }

  &__content {
    grid-column: 6 span;
    align-self: center;
    max-width: 550px;
    width: 100%;

    @include desktop {
      order: 1;
      grid-column: 12 span;
      max-width: 100%;
    }
  }

  &__subtitle {
    display: block;
    position: relative;
    color: var(--neutral-100);
    font-family: var(--ff-secondary);
    font-size: var(--fs-21);
    font-weight: 400;
    letter-spacing: 1.2px;
    text-indent: 50px;
    margin-bottom: 10px;

    &:after {
      content: '';
      width: 35px;
      height: 2px;
      background-color: var(--accent-color);
      border-radius: 5px;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
    }
  }

  &__title {
    color: var(--neutral-100);
    font-family: var(--ff-secondary);
    font-size: var(--fs-62);
    font-weight: 400;
    letter-spacing: 1.3px;
    margin: 0;
    margin-bottom: 24px;
    transition: font-size var(--trs-35);

    @include mobile {
      font-size: var(--fs-54);
      line-height: 1.2;
    }

    @include xs-small-mobile {
      font-size: var(--fs-42);
      line-height: 1.2;
    }
  }

  &__desc {
    color: var(--neutral-200);
    font-family: var(--ff-primary);
    font-size: var(--fs-18);
    font-weight: 400;
    margin: 0;
    margin-bottom: 42px;

    @include mobile {
      font-size: var(--fs-16);
      line-height: 1.4;
    }
  }

  &__buttons {
    @include flex-v-center;
    gap: 50px;
    margin-bottom: 50px;

    @include mobile {
      flex-direction: column;
    }
  }

  &__btn {
    @include mobile {
      width: 100%;
    }
  }

  &__read-link {
    position: relative;
    color: var(--neutral-100);
    font-family: var(--ff-primary);
    font-size: var(--fs-19);
    font-weight: 400;
    letter-spacing: 0.38px;

    &:before {
      content: '';
      width: 0%;
      height: 2px;
      background-color: var(--accent-color);
      border-radius: 5px;
      position: absolute;
      bottom: 0;
      left: 0;
      transition: width var(--trs-35);
      z-index: 10;
    }

    &:hover:before {
      width: 100%;
    }

    &:after {
      content: '';
      width: 100%;
      height: 2px;
      background-color: var(--neutral-100);
      border-radius: 5px;
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }

  &__images {
    grid-column: 6 span;
    margin-left: auto;
    position: relative;
    left: 0px;
    display: block;
    width: 100%;
    max-width: 540px;
    min-height: 100%;
    border: 10px solid hsl(43, 100%, 63%);

    @include desktop {
      grid-column: 12 span;
      margin-inline: auto;
    }
  }

  &__img {
    display: block;
    height: 100%;
    object-fit: contain;
    object-position: center center;

    position: relative;
    top: -30px;
    left: -30px;
    box-shadow: 0px 0px 20px 0px hsla(217, 57%, 25%, 0.3);
    transition: left var(--trs-35);

    @include small-tablet {
      left: 0px;
    }
  }
}

.specification {
  display: flex;
  gap: 40px;
  margin: 0;
  list-style: none;
  padding: 0;

  @include mobile {
    flex-direction: column;
    gap: 10px 0;
  }

  &__item {
    display: flex;
    flex-direction: column;
    gap: 0 10px;
    padding-left: 25px;

    @include mobile {
      flex-direction: row;
      align-items: baseline;
    }
  }

  &__meta {
    position: relative;
    color: var(--neutral-100);
    font-family: var(--ff-secondary);
    font-size: var(--fs-24);
    font-weight: 400;
    letter-spacing: 0.48px;
    margin-bottom: 12px;

    &:after {
      content: '';
      width: 16px;
      height: 16px;
      background-color: var(--accent-color);
      border-radius: 10em;
      position: absolute;
      top: 48%;
      left: -25px;
      transform: translateY(-50%);
    }
  }

  &__value {
    color: var(--neutral-200);
    font-family: var(--ff-primary);
    font-size: var(--fs-17);
    white-space: nowrap;
    font-weight: 400;
  }
}
