.header {
  background-color: var(--primary-color);
  min-height: 109px;
  @include flex-v-center;

  &__link-logo {
    margin-right: 35px;

    @include small-mobile {
      margin-right: auto;
    }

    @include xs-small-mobile {
      zoom: .8;
    }
  }

  &__btn {
    margin-left: 30px;

    @include small-tablet {
      display: none;
    }
  }
}

.social {
  display: flex;
  gap: 0.625rem;

  &--header {
    margin-right: auto;

    @include small-mobile {
      display: none;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2.5rem;
    height: 2.5rem;
    border: 2px solid var(--neutral-100);
    background-color: var(--neutral-100);
    fill: var(--primary-color);
    transition: fill var(--trs-35), border var(--trs-35), background-color var(--trs-35), box-shadow var(--trs-35);

    &:focus {
      outline: none;
    }

    @include hover {
      fill: var(--accent-color);
      border: 2px solid var(--accent-color);
      background-color: transparent;
    }

    &:active {
      fill: var(--accent-color);
      border: 2px solid var(--accent-color);
      background-color: transparent;
    }

    &.focus-visible {
      box-shadow: 0 0 0 3px var(--focus-visible);
    }
  }
}

.navigation {
  @include desktop {
    display: none;
  }
}

.nav {

  &__list {
    list-style: none;
    display: flex;
  }

  &__item:not(:last-child) {
    margin-right: 2.9375rem;
  }

  &__link {
    color: var(--neutral-100);
    font-family: var(--ff-primary);
    font-size: var(--fs-17);
    font-weight: 400;
    letter-spacing: 0.17px;
    transition: color var(--trs-35), box-shadow var(--trs-35);

    &--active {
      color: var(--accent-color);
    }

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--accent-color);
    }

    &:active {
      color: var(--accent-color);
    }

    &.focus-visible {
      box-shadow: 0 3px 0 0 var(--focus-visible);
    }
  }
}

.cart {
  position: relative;
  @include flex-all-center;
  width: 2.5rem;
  height: 2.5rem;
  border: 2px solid var(--neutral-100);
  background: var(--neutral-100);
  margin-left: 41px;
  cursor: pointer;
  fill: var(--primary-color);
  transition: fill var(--trs-35), border var(--trs-35), background-color var(--trs-35), box-shadow var(--trs-35);

  @include small-tablet {
    margin-left: 0px;
  }

  @include xs-small-mobile {
    zoom: .8;
  }

  &__counter {
    @include flex-all-center;
    width: 30px;
    height: 30px;
    background-color: var(--accent-color);
    border: 2px solid var(--primary-color);
    position: absolute;
    top: 1.7rem;
    right: 1.7rem;
    color: var(--primary-color);
    font-family: var(--ff-primary);
    font-size: var(--fs-16);
  }

  &:focus {
    outline: none;
  }

  @include hover {
    fill: var(--neutral-100);
    border: 2px solid var(--accent-color);
    background-color: transparent;
  }

  &:active {
    fill: var(--neutral-100);
    border: 2px solid var(--accent-color);
    background-color: transparent;
  }

  &.focus-visible {
    box-shadow: 0 0px 0 3px var(--focus-visible);
  }
}

.burger {
  display: none;
  margin-left: 30px;

  @include desktop {
    display: flex;
  }

  @include xs-small-mobile {
    margin-left: 20px;
  }

  &__check {
    cursor: pointer;
    position: relative;
    width: 40px;
    height: 45px;
    display: inline-block;
  }

  &__line {
    position: absolute;
    top: 50%;
    right: 0;
    width: 30px;
    height: 1px;
    display: inline-block;
    transform: translateY(-50%);
    background-color: var(--neutral-100);

    &:after {
      content: '';
      position: absolute;
      top: 12px;
      right: 0;
      width: 40px;
      height: 1px;
      display: inline-block;
      background-color: var(--neutral-100);
    }

    &:before {
      content: '';
      position: absolute;
      bottom: 12px;
      right: 0;
      width: 40px;
      height: 1px;
      display: inline-block;
      background-color: var(--neutral-100);
    }
  }
}

.checkbox {
  display: none;
}
