.btn {
  // Reset
  appearance: none;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-decoration: none !important;
  line-height: 1;
  white-space: nowrap;
  margin: 0;
  cursor: pointer;
  vertical-align: baseline;
  position: relative;
  user-select: none;
  z-index: 1;

  // Text
  font-family: var(--ff-secondary);
  font-size: 1.1875rem; // 19px;
  font-weight: 400;
  text-align: center;
  text-transform: none;

  // Appearance
  gap: 0.5em; // 8px
  border: 0.125em solid transparent; // 2px;
  border-radius: 0em; // 0px
  padding: 1.125rem 3.25rem; // 18px 52px;
  color: var(--primary-color);
  background-color: var(--accent-color);
  transition:color var(--trs-35), background-color var(--trs-35), border-color var(--trs-35);

  // Animations
  &:focus {
    outline: none;
  }

  &:hover,
  &:active {
    // box-shadow: inset 0 0 0 100px rgba(0, 0, 0, .2);
    color: var(--neutral-100);
    border-color: var(--accent-color);
    background-color: transparent;
  }

  // .btn.secondary
  &.secondary {
    color: var(--neutral-900);
    background-color: hsl(0, 0%, 80%);

    &:hover,
    &:active {
      color: var(--neutral-100);
      background-color: hsl(0, 0%, 20%);
    }
  }

  // .btn.outline
  &.outline {
    color: var(--primary-color);
    border-color: var(--accent-color);
    background-color: transparent;

    &:hover,
    &:active {
      color: var(--primary-color);
      border-color: var(--accent-color);
      background-color: var(--accent-color);
    }
  }

  // .btn.outline-light
  &.outline-light {
    color: var(--neutral-100);
    border-color: var(--accent-color);
    background-color: transparent;

    &:hover,
    &:active {
      color: var(--primary-color);
      border-color: var(--accent-color);
      background-color: var(--accent-color);
    }
  }

  // .btn.text
  &.text {
    color: var(--primary-color);
    background-color: transparent;

    &:hover,
    &:active {
      border-color: var(--primary-color);
    }
  }

  // sizes button
  &.sm {
    padding: 0.625em 1.25em; // 10px 20px;
    font-size: 0.875em; // 14px;
  }

  &.lg {
    padding: 1em 2em; // 16px 32px;
    font-size: 1.25em; // 20px;
  }

  &.wide {
    display: flex;
    width: 100%;

    >*:not(:first-child) {
      margin-left: auto;
    }
  }
}

.btn.focus-visible {
  box-shadow: 0 0 0 3px var(--focus-visible);
}
