.reader {
  padding: 130px 0;
  background-color: var(--neutral-100);
  transition: padding var(--trs-35);

  @include mobile {
    padding: 80px 0;
  }

  &__container {
    gap: 30px;

    @include mobile {
      gap: 0px;
    }
  }

  &__content {
    grid-column: 4 span;
    align-self: center;

    @include desktop {
      grid-column: 6 span;
    }

    @include small-tablet {
      grid-column: 12 span;
    }

    @include mobile {
      padding: 0 20px;
    }
  }

  &__title {
    color: var(--primary-color);
    font-family: var(--ff-secondary);
    font-size: var(--fs-42);
    font-weight: 400;
    margin: 0;
    margin-bottom: 23px;
  }

  &__desc {
    color: var(--neutral-300);
    font-family: var(--ff-primary);
    font-size: var(--fs-19);
    font-weight: 400;
    margin-top: 33px;
    margin-bottom: 33px;

    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
    overflow: hidden;

    @include desktop {
      display: -webkit-box;
      -webkit-line-clamp: 4;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }

    @include mobile {
      display: -webkit-box;
      -webkit-line-clamp: 8;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
  }
}

.rating {

  &__info {
    display: flex;
    align-items: baseline;
    gap: 10px;
    margin-bottom: 10px;
  }

  &__value {
    color: var(--primary-color);
    font-family: var(--ff-secondary);
    font-size: var(--fs-22);
    font-weight: 400;
  }

  &__text {
    color: var(--primary-color);
    font-family: var(--ff-primary);
    font-size: var(--fs-18);
    font-weight: 400;
  }
}

.comments {
  grid-column: 8 span;

  @include desktop {
    grid-column: 6 span;
  }

  @include small-tablet {
    grid-column: 12 span;
  }

  &__list {
    padding: 0;
    list-style: none;

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr 1fr;
    gap: 20px;
    grid-template-areas:
      "comments__item col"
      "comments__item col";


    @include desktop {
      display: flex;
      flex-direction: column;
    }
  }

  &__item {
    padding: 30px 35px;
    background-color: var(--primary-color);

    &--col {
      grid-area: col;
      align-self: center;
    }

    span {
      display: block;
      color: var(--neutral-100);
      font-family: var(--ff-secondary);
      font-size: var(--fs-22);
      font-weight: 400;
      text-align: left;
      margin-bottom: 15px;
    }
  }

  &__info {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-bottom: 20px;

    @include small-mobile {
      display: flex;
      flex-direction: column;
      align-items: baseline;
    }
  }

  &__img-avatar {
    border: 3px solid var(--neutral-100);
    border-radius: 100vw;
    background-color: var(--neutral-100);
  }

  &__info-text {
    display: flex;
    flex-direction: column;
  }

  &__info-title {
    color: var(--neutral-100);
    font-family: var(--ff-secondary);
    font-size: var(--fs-24);
    font-weight: 400;
    margin: 0;
    margin-bottom: 16px;
  }

  &__info-desc {
    color: var(--neutral-200);
    font-family: var(--ff-primary);
    font-size: var(--fs-18);
    font-weight: 400;
    margin: 0;

    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;

    @include mobile {
      display: -webkit-box;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
  }
}
