.author {
  &__title {
    color: var(--primary-color);
    font-family: var(--ff-secondary);
    font-size: var(--fs-42);
    font-weight: 400;
    text-align: center;
    margin: 0;
    margin-bottom: 23px;
  }

  &__list {
    margin: 0;
    gap: 20px;
    padding: 60px 0;

    @include mobile {
      gap: 40px 0;
    }
  }

  &__item {
    display: flex;
    gap: 30px;
    grid-column: 6 span;

    @include desktop {
      grid-column: 12 span;
      display: flex;
      justify-content: center;
    }

    @include mobile {
      flex-direction: column;
    }
  }

  &__img {
    display: block;
    height: 100%;
    object-fit: cover;
    object-position: center center;

    @include mobile {
      margin-inline: auto;
    }
  }

  &__content {
    width: 100%;
    max-width: 315px;
    align-self: center;

    @include desktop {
      max-width: 50%;
    }

    @include mobile {
      max-width: 100%;
      text-align: center;
    }
  }

  &__name {
    color: var(--primary-color);
    font-family: var(--ff-secondary);
    font-size: var(--fs-32);
    font-style: normal;
    font-weight: 400;
    letter-spacing: 0.32px;
    margin: 0;
    margin-bottom: 7px;
  }

  &__price {
    display: block;
    color: var(--accent-color);
    font-family: var(--ff-primary);
    font-size: var(--fs-21);
    font-weight: 400;
    margin-bottom: 13px;
  }

  &__desc {
    color: var(--neutral-300);
    font-family: var(--ff-primary);
    font-size: var(--fs-19);
    font-weight: 400;
    margin: 0;
    margin-bottom: 17px;

    @include mobile {
      max-width: 500px;
      margin-inline: auto;
      margin-bottom: 17px;
    }
  }

  &__printed {
    position: relative;
    display: inline-block;
    color: var(--primary-color);
    font-family: var(--ff-secondary);
    font-size: var(--fs-24);
    font-weight: 400;
    margin-bottom: 27px;
    text-indent: 25px;
    transition: color var(--trs-35), box-shadow var(--trs-35);

    &:after {
      content: '';
      width: 16px;
      height: 16px;
      flex-shrink: 0;
      border-radius: 10em;
      background-color: var(--accent-color);
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
    }

    &:focus {
      outline: none;
    }

    &:active {
      color: var(--accent-color);
    }

    &.focus-visible {
      box-shadow: 0 3px 0 0 var(--focus-visible);
    }

  }

  &__btn {
    transition: var(--trs-35);
    display: block;

    @include mobile {
      margin-inline: auto;
      width: 100%;
    }
  }
}
