.subscribe {
  padding: 95px 0;
  margin: 120px 0;
  background-color: var(--accent-color);
  transition: padding var(--trs-35), margin var(--trs-35);

  @include mobile {
    padding: 60px 0;
    margin: 80px 0;
  }

  &__title {
    color: var(--primary-color);
    text-align: center;
    font-family: var(--ff-secondary);
    font-size: var(--fs-42);
    font-weight: 400;
    margin: 0;
    margin-bottom: 24px;
  }

  &__desc {
    color: var(--primary-color);
    text-align: center;
    font-family: var(--ff-primary);
    font-size: var(--fs-18);
    font-weight: 400;
    margin: 0;
    max-width: 550px;
    margin-inline: auto;
    margin-top: 25px;
    margin-bottom: 50px;
  }

  &__form {
    width: 100%;
    display: flex;
    gap: 20px;

    @include small-tablet {
      flex-direction: column;
    }
  }

  &__label {
    width: 100%;
    padding: 20px 40px;
    background-color: var(--neutral-100);
  }

  &__input {
    width: 100%;
    background-color: transparent;
    color: var(--primary-color);
    font-family: var(--ff-primary);
    font-size: var(--fs-18);
    font-weight: 400;

    letter-spacing: 0.1px;
    text-align: left;
  }

  &__btn {
    color: var(--neutral-100);
    padding: 20px 125px;
    background-color: var(--primary-color);
    border: 2px solid transparent;
    transition: background-color var(--trs-35), fill var(--trs-35), border var(--trs-35), color var(--trs-35);

    @include hover {
      background-color: transparent;
      border: 2px solid var(--primary-color);
      fill: var(--accent-color);
      color: var(--primary-color);
    }
  }
}
