.brand {
  padding: 110px 0;
  background-color: var(--neutral-100);
  transition: padding var(--trs-35);

  @include mobile {
    padding: 70px 0;
  }

  &__title {
    color: var(--primary-color);
    text-align: center;
    font-family: var(--ff-secondary);
    font-size: var(--fs-42);
    font-weight: 400;
    margin: 0;
    margin-bottom: 23px;
  }

  &__list {
    gap: 47px;
    list-style: none;
    margin: 0;
    padding: 0;
    margin-top: 50px;
    text-align: center;

    @include mobile {
      gap: 50px 0px;
    }
  }

  &__item {
    grid-column: 3 span;

    @include desktop {
      grid-column: 6 span;
    }

    @include mobile {
      grid-column: 12 span;
    }
  }

  &__picture {
    display: block;
    width: 100%;
    max-width: 189px;
    height: 100px;
    overflow: hidden;
    margin-inline: auto;
  }

  &__img {
    display: block;
    height: 100%;
    object-fit: cover;
    object-position: center center;
  }

  &__name {
    color: var(--primary-color);
    text-align: center;
    font-family: var(--ff-secondary);
    font-size: var(--fs-24);
    font-weight: 400;
    margin: 0;
    margin: 19px 0;
  }

  &__desc {
    color: var(--neutral-300);
    text-align: center;
    font-family: var(--ff-primary);
    font-size: var(--fs-19);
    font-weight: 400;
    max-width: 290px;
    margin: 0;
    margin-inline: auto;

    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;

    @include desktop {
      max-width: 400px;
    }

    @include mobile {
      display: -webkit-box;
      -webkit-line-clamp: 4;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
  }
}
