:root {
  --ff-primary: "Inter", sans-serif;
  --ff-secondary: "Cardo", sans-serif;

  --content-width: 81.25rem; // 1300px;
  --container-offset: 0.9375rem; // 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  /* ------------------- */
  /* color theme         */
  /* ------------------- */
  --neutral-100: hsl(0, 0%, 100%);
  --neutral-200: hsl(218, 52%, 81%);
  --neutral-300: hsl(216, 5%, 61%);
  --accent-color: hsl(43, 100%, 63%);
  --primary-color: hsl(217, 57%, 25%);
  --focus-visible: hsl(221, 51%, 86%);
  --placeholder-color: hsl(0, 0%, 80%);
  --neutral-bg: hsl(214, 54%, 97%);
  /* ------------------- */
  /* font-size           */
  /* ------------------- */
  --fs-16: 1rem; // 16px;
  --fs-17: 1.0625rem; // 17px;
  --fs-18: 1.125rem; // 18px;
  --fs-19: 1.1875rem; // 19px;
  --fs-21: 1.3125rem; // 21px;
  --fs-22: 1.375rem; // 22px;
  --fs-23: 1.4375rem; // 23px;
  --fs-24: 1.5rem; // 24px;
  --fs-32: 2rem; // 32px;
  --fs-42: 2.625rem; // 42px;
  --fs-45: 2.8125rem; // 45px;
  --fs-54: 3.375rem; // 54px;
  --fs-62: 3.875rem; // 62px;
  /* ------------------- */
  /* transition          */
  /* ------------------- */
  --trs-25: .25s ease-in-out;
  --trs-35: .35s ease-in-out;
  --trs-45: .45s ease-in-out;
  /* ------------------- */
  /* margin              */
  /* ------------------- */
  --mrg-50: 3.125rem; // 50px;
  --mrg-100: 6.25rem; // 100px;
  --mrg-150: 9.375rem; // 150px;
  --mrg-200: 12.5rem; // 200px;
  /* ------------------- */
  /* gap                 */
  /* ------------------- */
  --gap: 1.25rem; // 20px;
}
