.articles {
  padding: 130px 0 150px 0;
  background-color: var(--neutral-bg);
  transition: padding var(--trs-35);

  @include mobile {
    padding: 70px 0 80px 0;
  }

  &__title {
    color: var(--primary-color);
    text-align: center;
    font-family: var(--ff-secondary);
    font-size: var(--fs-42);
    font-weight: 400;
    margin: 0;
    margin-bottom: 23px;
  }

  &__list {
    margin-top: 60px;
    padding: 0;
    gap: 20px;
    list-style: none;

    @include desktop {
      grid-template-columns: repeat(1, 1fr);
    }

    @include mobile {
      gap: 20px 0;
    }
  }

  &__item {
    grid-column: 4 span;
    background-color: var(--neutral-100);
    box-shadow: 0px 0px 13px 0px hsla(0, 0%, 0%, 0.07);

    @include desktop {
      display: flex;
      grid-column: 1 span;
    }

    @include small-tablet {
      display: grid;
    }
  }

  &__picture {
    display: block;
    overflow: hidden;

    @include desktop {
      width: 100%;
      max-width: 420px;
    }

    @include small-tablet {
      max-width: 100%;
    }
  }

  &__img {
    width: 100%;
    max-width: 420px;
    display: block;
    height: 100%;
    object-fit: cover;
    object-position: center center;
    transition: transform .50s ease-in-out;

    @include hover {
      transform: scale(1.05);
    }

    @include small-tablet {
      max-width: 100%;
    }
  }

  &__content {
    padding: 25px 30px;
    transition: padding var(--trs-35);

    @include desktop {
      width: 100%;
      display: flex;
      flex-direction: column;
    }

    @include xs-small-mobile {
      padding: 25px 20px;
    }
  }

  &__content-title {
    color: var(--primary-color);
    font-family: var(--ff-secondary);
    font-size: var(--fs-24);
    font-weight: 400;
    text-align: left;
    margin: 0;
    margin-bottom: 20px;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }

  &__content-desc {
    color: var(--neutral-300);
    font-family: var(--ff-primary);
    font-size: var(--fs-19);
    font-weight: 400;
    text-align: left;
    margin: 0;
    margin-bottom: 15px;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }

  &__content-bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: auto;

    a {
      position: relative;
      color: var(--primary-color);
      font-family: var(--ff-secondary);
      font-size: var(--fs-19);
      font-weight: 400;
      line-height: 1.4375rem;
      letter-spacing: 0.3799999952316284px;
      text-align: left;
      padding-right: 10px;

      &:before {
        content: '';
        width: 0%;
        height: 2px;
        background-color: var(--accent-color);
        border-radius: 5px;
        position: absolute;
        bottom: 0;
        left: 0;
        transition: width var(--trs-35);
        z-index: 10;
      }

      &:hover:before {
        width: 100%;
      }

      &:after {
        content: '';
        width: 100%;
        height: 2px;
        background-color: var(--primary-color);
        border-radius: 5px;
        position: absolute;
        bottom: 0;
        left: 0;
      }
    }

    time {
      color: var(--primary-color);
      font-family: var(--ff-primary);
      font-size: var(--fs-16);
      font-weight: 400;
      text-align: left;
    }
  }
}
