.extension {
  padding: 116px 0 130px 0;
  background-color: var(--neutral-bg);
  transition: padding var(--trs-35);

  @include mobile {
    padding: 60px 0 70px 0;
  }

  &__title {
    color: var(--primary-color);
    text-align: center;
    font-family: var(--ff-secondary);
    font-size: var(--fs-42);
    font-weight: 400;
    margin: 0;
    margin-bottom: 23px;
  }

  &__list {
    gap: 30px;
    padding: 0;
    list-style: none;
    margin: 0;
    margin: 70px 0 60px 0;

    @include small-tablet {
      gap: 0px;
    }
  }

  &__item {
    grid-column: 6 span;
    padding: 40px;
    background-color: var(--neutral-100);
    transition: padding var(--trs-35);

    @include small-tablet {
      grid-column: 12 span;
    }

    @include xs-small-mobile {
      padding: 40px 20px;
    }
  }

  &__item-title {
    color: var(--primary-color);
    font-family: var(--ff-secondary);
    font-size: var(--fs-24);
    font-weight: 400;
    margin: 0;
    margin-bottom: 21px;
  }

  &__item-desc {
    color: var(--neutral-300);
    font-family: var(--ff-primary);
    font-size: var(--fs-19);
    font-weight: 400;
    margin: 0;
    margin-bottom: 10px;
  }

  &__btn {
    display: block;
    margin-inline: auto;
    margin-bottom: 34px;

    @include mobile {
      width: 100%;
      padding: 1.125rem 0;
    }
  }

  &__text {
    display: block;
    color: var(--neutral-300);
    text-align: center;
    font-family: var(--ff-primary);
    font-size: var(--fs-18);
    font-weight: 400;
    margin: 0;
  }

  &__link {
    color: var(--primary-color);
    text-align: center;
    font-family: var(--ff-primary);
    font-size: var(--fs-18);
    font-weight: 400;
    letter-spacing: 0.19px;
    margin: 0;

    a {
      color: var(--accent-color);
    }
  }
}

.info {
  display: flex;
  gap: 30px;

  @include desktop {
    display: grid;
    grid-template-columns: 1fr;
    gap: 10px;
    margin-top: 20px;
  }

  &__pages,
  &__length {
    position: relative;
    display: flex;
    align-items: baseline;
    gap: 10px;
    padding-left: 26px;

    &:after {
      content: '';
      width: 16px;
      height: 16px;
      background-color: var(--accent-color);
      border-radius: 10em;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
    }
  }

  &__pages-meta {
    color: var(--primary-color);
    font-family: var(--ff-secondary);
    font-size: var(--fs-22);
    font-weight: 400;
    margin: 0;
  }

  &__pages-value {
    color: var(--neutral-300);
    font-family: var(--ff-primary);
    font-size: var(--fs-17);
    font-weight: 400;
    margin: 0;
  }
}
