.get-book {
  padding: 70px 0;
  background-color: var(--primary-color);

  &__container {
    gap: 70px;

    @include desktop {
      gap: 50px 0;
    }
  }

  &__content {
    grid-column: 6 span;
    align-self: center;

    @include desktop {
      order: 1;
      grid-column: 12 span;
      margin-inline: auto;
    }

    @include mobile {
      text-align: center;
    }
  }

  &__title {
    color: var(--neutral-100);
    font-family: var(--ff-secondary);
    font-size: var(--fs-45);
    font-weight: 400;
    margin: 0;
    margin-bottom: 27px;
  }

  &__descr {
    color: var(--neutral-200);
    font-family: var(--ff-primary);
    font-size: var(--fs-19);
    font-weight: 400;
    width: 100%;
    max-width: 610px;
    margin: 0;
    margin-top: 27px;
    margin-bottom: 27px;

    @include desktop {
      max-width: 886px;
    }
  }

  &__picture {
    grid-column: 6 span;
    margin-left: auto;

    @include desktop {
      grid-column: 12 span;
      margin-inline: auto;
    }
  }

  &__img {
    display: block;
    height: 100%;
    object-fit: cover;
    object-position: center center;
    padding: 0 20px;
  }

  &__btn {
    @include mobile {
      width: 100%;
    }
  }
}

.decor-line {
  &--cntr {
    @include mobile {
      margin-inline: auto;
    }
  }
}
