.footer {
  padding-top: 100px;
  background-color: var(--primary-color);
  transition: padding-top var(--trs-35);

  @include mobile {
    padding-top: 60px;
  }

  &__item:nth-of-type(1) {
    @include desktop {
      width: 100%;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr) 1.2fr;
    gap: 40px;
    padding-bottom: 70px;
    padding: 0;
    list-style: none;
    margin: 0;
    transition: padding-bottom var(--trs-35);

    @include desktop {
      display: flex;
      flex-wrap: wrap;
      gap: 40px 80px;
      padding-bottom: 70px;
    }

    @include mobile {
      padding-bottom: 30px;
    }
  }

  &__link-logo {
    display: block;
    margin-bottom: 10px;
  }

  &__item-title {
    color: var(--neutral-100);
    font-family: var(--ff-secondary);
    font-size: var(--fs-24);
    font-weight: 400;
    letter-spacing: 0.3840000033378601px;
    text-align: left;
    margin: 0;
    margin-bottom: 25px;
  }

  &__link {
    position: relative;
    display: flex;
    align-items: center;
    gap: 20px;
    padding: 4px 0;
    padding-left: 10px;
    color: hsla(218, 52%, 81%, 1);
    font-family: var(--ff-primary);
    font-size: var(--fs-17);
    font-weight: 400;
    letter-spacing: 0.1px;
    text-align: left;
    transition: color var(--trs-35);

    @include hover {
      color: var(--neutral-100);
    }
  }

  &__contact:not(:last-child) {
    margin-bottom: 20px;
  }

  &__contact {
    display: flex;
    gap: 20px;
    font-style: normal;
  }

  &__contact-left {
    width: 65px;
    color: var(--neutral-100);
    font-family: var(--ff-secondary);
    font-size: var(--fs-18);
    font-weight: 400;
    letter-spacing: 0.3px;
    text-align: left;
  }

  &__contact-right {
    color: hsla(218, 52%, 81%, 1);
    font-family: var(--ff-primary);
    font-size: var(--fs-17);
    font-weight: 400;
    text-align: left;

    &--color {
      transition: color var(--trs-35);

      @include hover {
        color: var(--accent-color);
      }
    }
  }

  &__bottom {
    padding: 25px 0;
    color: var(--neutral-200);
    font-family: var(--ff-primary);
    font-size: var(--fs-17);
    font-weight: 400;
    letter-spacing: 0.1;
    text-align: center;

    span {
      color: var(--neutral-100);
    }
  }
}

.social {
  &__link {
    &--color {
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: transparent;
      border: 2px solid var(--accent-color);
      fill: var(--neutral-100);

      @include hover {
        fill: var(--primary-color);
        background-color: var(--neutral-100);
        border: 2px solid transparent;
      }
    }
  }
}

.fx-circle {
  position: relative;
  width: 6px;
  height: 6px;
  background-color: var(--accent-color);
  border-radius: 10em;

  &:after {
    content: '';
    width: 6px;
    height: 6px;
    background-color: var(--accent-color);
    border-radius: 10em;
    position: absolute;
    top: 0%;
    left: -8px;
    transform: translateX(-50%);
  }

  &:before {
    content: '';
    width: 6px;
    height: 6px;
    background-color: var(--accent-color);
    border-radius: 10em;
    position: absolute;
    top: 0%;
    left: 14px;
    transform: translateX(-50%);
  }
}
