.skills {
  padding: 120px 0;
  background-color: var(--neutral-100);
  transition: padding var(--trs-35);

  @include mobile {
    padding: 80px 0;
  }

  &__title {
    color: var(--primary-color);
    text-align: center;
    font-family: var(--ff-secondary);
    font-size: var(--fs-42);
    font-weight: 400;
    margin: 0;
    margin-bottom: 23px;
  }

  &__content {
    margin-top: 80px;
    gap: 50px;

    @include mobile {
      gap: 50px 0;
    }
  }

  &__list {
    list-style: none;
    padding: 0;
    grid-column: 6 span;
    counter-reset: counter;
    align-self: center;
    gap: 10px;

    @include desktop {
      order: 1;
      grid-column: 12 span;
    }
  }

  &__item {
    grid-column: 6 span;
    background-color: var(--neutral-bg);
    padding: 30px;

    @include mobile {
      grid-column: 12 span;
    }
  }

  &__counter {
    display: block;
    margin-bottom: 30px;

    &:after {
      counter-increment: counter;
      content: counter(counter, decimal-leading-zero);
      width: 50px;
      height: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: var(--accent-color);
      border-radius: 100vw;
    }
  }

  &__descr {
    color: var(--primary-color);
    font-family: var(--ff-secondary);
    font-size: var(--fs-24);
    font-weight: 400;
    margin: 0;

    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }

  &__picture {
    grid-column: 6 span;
    position: relative;
    display: block;
    max-width: 555px;
    width: 100%;
    height: 100%;
    border: 10px solid var(--accent-color);
    margin-left: auto;

    @include desktop {
      grid-column: 12 span;
      margin-inline: auto;
    }
  }

  &__img {
    position: relative;
    top: -30px;
    left: -30px;
    box-shadow: 0px 0px 20px 0px hsla(217, 57%, 25%, 0.3);
    transition: left var(--trs-35);

    @include small-tablet {
      left: 0px;
    }
  }
}
